import React, { useEffect, useState } from 'react';
import Papa from 'papaparse';
import { Chart as ChartJS, CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend, PointElement } from 'chart.js';
import { BoxPlotController, BoxAndWiskers } from '@sgratzl/chartjs-chart-boxplot';
import { Chart, Scatter } from 'react-chartjs-2';
import './CropStatusCheck.css';

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
  PointElement,
  BoxPlotController,
  BoxAndWiskers
);

const stages = [
  { value: 1.0, label: '' },
  { value: 2.0, label: '' },
  { value: 3.0, label: '' },
  { value: 4.0, label: '' },
  { value: 5.0, label: '' },
];

const fetchCSVData = async (url) => {
  const response = await fetch(url);
  const reader = response.body.getReader();
  const result = await reader.read();
  const decoder = new TextDecoder('utf-8');
  const csv = decoder.decode(result.value);

  return new Promise((resolve) => {
    Papa.parse(csv, {
      header: true,
      dynamicTyping: true,
      complete: (results) => {
        resolve(results.data);
      }
    });
  });
};

const calculateBoxPlotStats = (values) => {
  values.sort((a, b) => a - b);
  const min = values[0];
  const max = values[values.length - 1];
  const median = values[Math.floor(values.length / 2)];
  const quantile1 = values[Math.floor(values.length / 4)];
  const quantile3 = values[Math.floor((values.length * 3) / 4)];
  const mean = (values.reduce((acc, val) => acc + val, 0) / values.length).toFixed(2);

  return { min, quantile1, median, mean, quantile3, max };
};

const DetailView = ({ bedData }) => {
  const fields = [
    { key: 'Stem Length', label: ' ' },
    { key: 'Leaf Count', label: ' ' },
    { key: 'Leaf Width', label: ' ' },
    { key: 'Leaf Length', label: ' ' },
    { key: 'Stem Thickness', label: ' ' }
  ];

  const getBoxPlotStats = (data) => {
    return fields.map(field => {
      const values = data
        .map(crop => crop[field.key])
        .filter(value => value !== undefined && value !== null);
      const stats = calculateBoxPlotStats(values);
      return {
        label: field.label,
        ...stats
      };
    });
  };

  return (
    <div className="detail-view">
      <div className="detail-box">
        <h4>  </h4>
        <table className="boxplot-table">
          <thead>
            <tr>
              <th></th>
              {fields.map(field => (
                <th key={field.key}>{field.label}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            {['min', 'quantile1', 'median', 'mean', 'quantile3', 'max'].map((statKey) => (
              <tr key={statKey}>
                <td>{{
                  min: '',
                  quantile1: '1',
                  median: '',
                  mean: '',
                  quantile3: '3',
                  max: ''
                }[statKey]}</td>
                {getBoxPlotStats(bedData).map(stat => (
                  <td key={stat.label}>{stat[statKey]}</td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

const CropStatusCheck = () => {
  const [data1, setData1] = useState([]);
  const [data2, setData2] = useState([]);
  const [data3, setData3] = useState([]);
  const [selectedBed, setSelectedBed] = useState('1 ');
  const [showTable, setShowTable] = useState(false);
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    fetchCSVData('data1.csv').then(setData1);
    fetchCSVData('data2.csv').then(setData2);
    fetchCSVData('data3.csv').then(setData3);
  }, []);

  const handleShowTable = () => {
    setShowTable(!showTable);
    if (!showTable) {
      setIsLoading(true);
      setTimeout(() => {
        setIsLoading(false);
      }, 2000); // 2  "AI  "  
    }
  };

  const fields = [
    { key: 'Stem Length', label: ' ' },
    { key: 'Leaf Count', label: ' ' },
    { key: 'Leaf Width', label: ' ' },
    { key: 'Leaf Length', label: ' ' },
    { key: 'Stem Thickness', label: ' ' }
  ];

  const boxPlotOptions = (title) => ({
    responsive: true,
    plugins: {
      legend: {
        position: 'top',
      },
      title: {
        display: true,
        text: title
      }
    },
    scales: {
      x: { title: { display: false } },
      y: { title: { display: false, text: 'Values' } }
    }
  });

  const DotPlotChart = ({ data, title }) => {
    const groupedData = stages.map(stage => {
      const values = data.filter(value => value['Growth Stage'] === stage.value);
      return values.map((value, index) => ({
        x: index - (values.length - 1) / 2,
        y: stage.label
      }));
    }).flat();

    const totalDataCount = data.length;

    const chartData = {
      datasets: [
        {
          label: ' ',
          data: groupedData,
          backgroundColor: 'rgba(75, 192, 192, 0.6)',
          borderColor: 'rgba(75, 192, 192, 1)',
          pointRadius: 5,
        }
      ]
    };

    const options = {
      responsive: true,
      plugins: {
        legend: {
          position: 'top',
        },
        title: {
          display: true,
          text: `${title} (${totalDataCount} )`
        },
        tooltip: {
          callbacks: {
            label: function (tooltipItem) {
              const stageValue = stages.find(stage => stage.label === tooltipItem.raw.y).value;
              const count = data.filter(value => value['Growth Stage'] === stageValue).length;
              return `${tooltipItem.raw.y}: ${count} `;
            }
          },
          mode: 'nearest',
          intersect: false,
          displayColors: false
        }
      },
      scales: {
        x: {
          type: 'linear',
          ticks: {
            display: false
          }
        },
        y: {
          type: 'category',
          labels: stages.map(stage => stage.label).reverse()
        }
      }
    };

    return (
      <div style={{ width: '100%', height: '400px', position: 'relative' }}>
        <Scatter data={chartData} options={options} />
      </div>
    );
  };

  const BoxPlotChart = ({ data, title }) => {
    const chartData = {
      labels: fields.map(field => field.label),
      datasets: [{
        label: '  ',
        data: fields.map(field => data.map(row => row[field.key])),
        backgroundColor: 'rgba(138, 43, 226, 0.5)',
        borderColor: 'rgba(138, 43, 226, 1)',
        borderWidth: 1,
        outlierColor: '#999999'
      }]
    };

    return (
      <div style={{ width: '100%', maxWidth: '800px', height: '400px', position: 'relative', margin: '0 auto' }}>
        <Chart
          type="boxplot"
          data={chartData}
          options={boxPlotOptions(title)}
        />
      </div>
    );
  };

  const handleBedSelection = (bed) => {
    setSelectedBed(bed);
  };

  const renderCharts = (bedData, bedTitle) => (
    <div className="chart-container">
      <BoxPlotChart data={bedData} title={`  `} />
      {showTable && !isLoading && <DotPlotChart data={bedData} title={`  `} />}
    </div>
  );

  const bedDataMap = {
    '1 ': { data: data1 },
    '2 ': { data: data2 },
    '3 ': { data: data3 },
  };

  return (
    <div className="crop-status-check">
      <div className="header">
        <h2>   </h2>
        <div className="button-container">
          <button onClick={handleShowTable} className="detail-button">
            {showTable ? 'AI ' : 'AI'}
          </button>
          <button className={selectedBed === '1 ' ? 'active' : ''} onClick={() => handleBedSelection('1 ')}>1 </button>
          <button className={selectedBed === '2 ' ? 'active' : ''} onClick={() => handleBedSelection('2 ')}>2 </button>
          <button className={selectedBed === '3 ' ? 'active' : ''} onClick={() => handleBedSelection('3 ')}>3 </button>
        </div>
      </div>
      <div className="graph-box">
        {isLoading ? (
          <div className="loading-message">AI  ...</div>
        ) : (
          renderCharts(bedDataMap[selectedBed].data, selectedBed)
        )}
        {showTable && !isLoading && <DetailView bedData={bedDataMap[selectedBed].data} />}
      </div>
    </div>
  );
};

export default CropStatusCheck;
