import React from 'react';
import { useNavigate } from 'react-router-dom';

function Home() {
  const navigate = useNavigate();

  return (
    <div 
      style={{ 
        display: 'flex',
        flexDirection: 'column', 
        alignItems: 'center', 
        textAlign: 'center', 
        minHeight: '100vh', 
        color: 'black', 
        padding: '20px',
        boxSizing: 'border-box'
      }}
    >
      {/*  */}
      <div style={{
        padding: '20px',
        border: '3px solid green', 
        borderRadius: '8px', 
        backgroundColor: 'rgba(255, 255, 255, 0.8)', 
        marginBottom: '20px',
        maxWidth: '800px', 
        width: '100%' 
      }}>
        <p style={{ padding: '0px', fontSize: '1.2em', marginBottom: '8px' }}>
             ,  .
        </p>
        <p style={{ padding: '0px', fontSize: '1.2em', marginTop: '0' }}>
                 !
        </p>
      </div>

      {/*   */}
      <img 
        src="/background3.jpg" 
        alt="Central" 
        style={{ width: '100%', maxWidth: '800px', height: 'auto', marginBottom: '20px' }} 
      />

      {/*  */}
      <div style={{ display: 'flex', justifyContent: 'center', flexWrap: 'wrap', gap: '20px', marginTop: '20px', width: '100%', maxWidth: '800px' }}>
        <button 
          style={{ 
            backgroundColor: 'white', 
            border: 'none', 
            color: 'black', 
            fontSize: '1em', 
            textAlign: 'left', 
            cursor: 'pointer', 
            padding: '10px 20px', 
            display: 'flex', 
            flexDirection: 'row', 
            alignItems: 'center', 
            boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.2)', 
            width: 'calc(33.33% - 20px)',
            minWidth: '200px'
          }}
          onClick={() => navigate('/environment')}
        >
          <img 
            src="/WetherRecommend.png" 
            alt="Environment" 
            style={{ width: '50px', height: 'auto', marginRight: '10px' }} 
          />
           
        </button>
        <button 
          style={{ 
            backgroundColor: 'white', 
            border: 'none', 
            color: 'black', 
            fontSize: '1em', 
            textAlign: 'left', 
            cursor: 'pointer', 
            padding: '10px 20px', 
            display: 'flex', 
            flexDirection: 'row', 
            alignItems: 'center', 
            boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.2)', 
            width: 'calc(33.33% - 20px)',
            minWidth: '200px'
          }}
          onClick={() => navigate('/crop-status')}
        >
          <img 
            src="/GrowthLevel.png" 
            alt="Crop Status" 
            style={{ width: '50px', height: 'auto', marginRight: '10px' }} 
          />
            
        </button>
        <button 
          style={{ 
            backgroundColor: 'white', 
            border: 'none', 
            color: 'black', 
            fontSize: '1em', 
            textAlign: 'left', 
            cursor: 'pointer', 
            padding: '10px 20px', 
            display: 'flex', 
            flexDirection: 'row', 
            alignItems: 'center', 
            boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.2)', 
            width: 'calc(33.33% - 20px)',
            minWidth: '200px'
          }}
          onClick={() => navigate('/crop-growth')}
        >
          <img 
            src="/GrowthPredict.png" 
            alt="Crop Growth" 
            style={{ width: '50px', height: 'auto', marginRight: '10px' }} 
          />
            
        </button> 
      </div>
    </div>
  );
}

export default Home;
