import React, { useState, useEffect } from 'react';
import { Line } from 'react-chartjs-2';
import Papa from 'papaparse';
import 'chart.js/auto';
import './EnvironmentPrediction.css';

const EnvironmentPrediction = () => {
  const [inputData, setInputData] = useState({
    stemLength: 0,
    leafCount: 0,
    leafWidth: 0,
    leafLength: 0,
    stemThickness: 0,
    fruitWeight: 0,
    internalTemp: 0,
    internalHumidity: 0,
    ph: 0,
    ec: 0,
    nutrientTemp: 0,
  });

  const [recommendedValues, setRecommendedValues] = useState({
    recommended_ti: 0,
    recommended_hi: 0,
    recommended_ph: 0,
    recommended_ec: 0,
    recommended_nutrient_temp: 0,
  });

  const [loading, setLoading] = useState(false);
  const [showRecommendations, setShowRecommendations] = useState(false);
  const [selectedBed, setSelectedBed] = useState('Bed_1'); //     

  const [graphData, setGraphData] = useState({
    temperature: [],
    humidity: [],
    recommendedTemp: [],
    recommendedHumidity: [],
    ph: [],
    ec: [],
    nutrientTemp: [],
    recommendedPh: [],
    recommendedEc: [],
    recommendedNutrientTemp: [],
    labels: [],
  });

  useEffect(() => {
    fetchData();
    const interval = setInterval(() => {
      fetchData();
    }, 60000);
    return () => clearInterval(interval);
  }, [selectedBed]); //      

  const fetchData = async () => {
    try {
      const response = await fetch('data4.csv');
      const text = await response.text();
      
      Papa.parse(text, {
        header: true,
        complete: (results) => {
          const data = results.data.filter(entry => entry.crops_id === selectedBed); //    
          const lastEntry = data[data.length - 1] || {};
          const labels = data.map(entry => entry.measured_date);

          setInputData({
            stemLength: parseFloat(lastEntry.stem_length) || 0,
            leafCount: parseFloat(lastEntry.leaf_cnt) || 0,
            leafWidth: parseFloat(lastEntry.leaf_width) || 0,
            leafLength: parseFloat(lastEntry.leaf_length) || 0,
            stemThickness: parseFloat(lastEntry.stem_thick) || 0,
            fruitWeight: parseFloat(lastEntry.fr1_cnt) || 0,
            internalTemp: parseFloat(lastEntry.ti_value) || 0,
            internalHumidity: parseFloat(lastEntry.hi_value) || 0,
            ph: parseFloat(lastEntry.pl_value) || 0,
            ec: parseFloat(lastEntry.ei_value) || 0,
            nutrientTemp: parseFloat(lastEntry.tl_value) || 0,
          });

          setRecommendedValues({
            recommended_ti: parseFloat(lastEntry.recommended_ti) || 0,
            recommended_hi: parseFloat(lastEntry.recommended_hi) || 0,
            recommended_ph: parseFloat(lastEntry.recommended_ph) || 0,
            recommended_ec: parseFloat(lastEntry.recommended_ec) || 0,
            recommended_nutrient_temp: parseFloat(lastEntry.recommended_nutrient_temp) || 0,
          });

          const lastWeekData = data.slice(-7);
          setGraphData({
            temperature: lastWeekData.map(entry => parseFloat(entry.ti_value) || 0),
            humidity: lastWeekData.map(entry => parseFloat(entry.hi_value) || 0),
            recommendedTemp: lastWeekData.map(entry => parseFloat(entry.recommended_ti) || 0),
            recommendedHumidity: lastWeekData.map(entry => parseFloat(entry.recommended_hi) || 0),
            ph: lastWeekData.map(entry => parseFloat(entry.pl_value) || 0),
            ec: lastWeekData.map(entry => parseFloat(entry.ei_value) || 0),
            nutrientTemp: lastWeekData.map(entry => parseFloat(entry.tl_value) || 0),
            recommendedPh: lastWeekData.map(entry => parseFloat(entry.recommended_ph) || 0),
            recommendedEc: lastWeekData.map(entry => parseFloat(entry.recommended_ec) || 0),
            recommendedNutrientTemp: lastWeekData.map(entry => parseFloat(entry.recommended_nutrient_temp) || 0),
            labels: lastWeekData.map(entry => entry.measured_date),
          });
        },
      });
    } catch (error) {
      console.error('Error fetching data:', error);
    }
  };

  const fetchRecommendedValues = () => {
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
      setShowRecommendations(true);
    }, 2000);
  };

  const handleBedSelection = (bed) => {
    setSelectedBed(bed);
  };

  const createChartData = (labels, data1, data2, label1, label2, color1, color2) => {
    const datasets = [
      {
        label: label1,
        data: data1,
        borderColor: color1,
        backgroundColor: `${color1}33`,
        yAxisID: 'y1',
      },
      {
        label: label2,
        data: data2,
        borderColor: color2,
        backgroundColor: `${color2}33`,
        yAxisID: 'y1',
      },
    ];

    return {
      labels,
      datasets,
    };
  };

  const createOptions = (range1) => {
    return {
      scales: {
        y1: {
          type: 'linear',
          position: 'left',
          min: range1.min,
          max: range1.max,
          title: {
            display: true,
            text: range1.label,
          },
        },
      },
      plugins: {
        tooltip: {
          callbacks: {
            label: (context) => {
              let label = context.dataset.label || '';
              if (label) {
                label += ': ';
              }
              label += context.raw;
              return label;
            },
          },
        },
      },
      elements: {
        point: {
          radius: 3,
        },
      },
      responsive: true,
      devicePixelRatio: 2, //    devicePixelRatio 
    };
  };

  const handleClick = (e, element) => {
    if (element.length > 0) {
      const { index } = element[0];
      const date = graphData.labels[index];
      const temp = graphData.temperature[index];
      const humidity = graphData.humidity[index];
      alert(`: ${date}\n: ${temp}C\n: ${humidity}%`);
    }
  };

  return (
    <div className="environment-prediction">
      <div className="header">
        <h2>   </h2>
        <div className="button-container">
          <button onClick={fetchRecommendedValues}>AI </button>
          <button className={selectedBed === 'Bed_1' ? 'active' : ''} onClick={() => handleBedSelection('Bed_1')}>1 </button>
          <button className={selectedBed === 'Bed_2' ? 'active' : ''} onClick={() => handleBedSelection('Bed_2')}>2 </button>
          <button className={selectedBed === 'Bed_3' ? 'active' : ''} onClick={() => handleBedSelection('Bed_3')}>3 </button>
        </div>
      </div>
      {loading && <div className="loading">AI  ...</div>}
      {showRecommendations && (
        <div className="suggestions">
          <p> {(recommendedValues.recommended_ti - inputData.internalTemp).toFixed(1)} {recommendedValues.recommended_ti - inputData.internalTemp > 0 ? '' : ''}  .</p>
          <p> {(recommendedValues.recommended_hi - inputData.internalHumidity).toFixed(1)}% {recommendedValues.recommended_hi - inputData.internalHumidity > 0 ? '' : ''}  .</p>
        </div>
      )}
      <div className="current-recommended-values">
        <div className="current">
          <h3> </h3>
          <p> : {inputData.internalTemp}C</p>
          <p> : {inputData.internalHumidity}%</p>
          <p>pH: {inputData.ph}</p>
          <p>EC: {inputData.ec} dS/m</p>
          <p> : {inputData.nutrientTemp}C</p>
        </div>
        {showRecommendations && (
          <div className="recommended">
            <h3> </h3>
            <p>  : {recommendedValues.recommended_ti}C</p>
            <p>  : {recommendedValues.recommended_hi}%</p>
            <p> pH: {recommendedValues.recommended_ph}</p>
            <p> EC: {recommendedValues.recommended_ec} dS/m</p>
            <p>  : {recommendedValues.recommended_nutrient_temp}C</p>
          </div>
        )}
      </div>

      <div className="graph-section">
        <div className="graph-box">
          <div className="graph-row">
            <div className="graph-container">
              <h3>   </h3>
              <Line
                data={createChartData(
                  graphData.labels,
                  graphData.temperature,
                  showRecommendations ? graphData.recommendedTemp : [],
                  '  (C)',
                  '   (C)',
                  '#FF5733',
                  '#1E90FF'
                )}
                options={createOptions({ min: 0, max: 50, label: '  (C)' })}
                onClick={handleClick}
              />
            </div>
            <div className="graph-container">
              <h3>   </h3>
              <Line
                data={createChartData(
                  graphData.labels,
                  graphData.humidity,
                  showRecommendations ? graphData.recommendedHumidity : [],
                  '  (%)',
                  '   (%)',
                  '#FF5733',
                  '#1E90FF'
                )}
                options={createOptions({ min: 0, max: 100, label: '  (%)' })}
                onClick={handleClick}
              />
            </div>
          </div>
        </div>
        <div className="graph-box">
          <div className="graph-row">
            <div className="graph-container">
              <h3>  pH</h3>
              <Line
                data={createChartData(
                  graphData.labels,
                  graphData.ph,
                  showRecommendations ? graphData.recommendedPh : [],
                  'pH',
                  ' pH',
                  '#32CD32',
                  '#DDA0DD'
                )}
                options={createOptions({ min: 4, max: 8, label: 'pH' })}
                onClick={handleClick}
              />
            </div>
            <div className="graph-container">
              <h3>  EC</h3>
              <Line
                data={createChartData(
                  graphData.labels,
                  graphData.ec,
                  showRecommendations ? graphData.recommendedEc : [],
                  'EC (dS/m)',
                  ' EC (dS/m)',
                  '#32CD32',
                  '#DDA0DD'
                )}
                options={createOptions({ min: 0, max: 5, label: 'EC (dS/m)' })}
                onClick={handleClick}
              />
            </div>
          </div>
        </div>
        <div className="graph-box">
          <div className="graph-row">
            <div className="graph-container">
              <h3>   </h3>
              <Line
                data={createChartData(
                  graphData.labels,
                  graphData.nutrientTemp,
                  showRecommendations ? graphData.recommendedNutrientTemp : [],
                  '  (C)',
                  '   (C)',
                  '#32CD32',
                  '#DDA0DD'
                )}
                options={createOptions({ min: 0, max: 50, label: '  (C)' })}
                onClick={handleClick}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default EnvironmentPrediction;
